  background-color: #faebd7;
}

.img-size {
  width: 200px;
}

.productsContainer {
  width: 53.5%;
  text-align: center;
  flex-wrap: wrap;
  gap: 2px;
  margin: auto;
  display: flex;
}

.productsContainer div {
  border: 1px solid #000;
}

